import { useEffect, useState } from "react";
import LoadingButton from '@mui/lab/LoadingButton';
import { fetchLastStories } from "../../services/storyService";
import { IStory } from "../../types/IStory";
import StarIcon from '@mui/icons-material/Star';
import Replay from '@mui/icons-material/ReplayRounded'
import { orange } from '@mui/material/colors';
import { Link } from 'react-router-dom';

const Main = function () {
  const [news, setNews] = useState([] as IStory[])
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    const newNews = JSON.parse(window.localStorage.getItem('news') || '[]')
    setNews(newNews);
    console.log('new news', newNews)
  }, []);

  useEffect(() => {
    window.localStorage.setItem('news', JSON.stringify(news));
  }, [news]);

  let intervalId:NodeJS.Timer

  useEffect(() => {
    updateStories()
    let isUpdating = false
    // eslint-disable-next-line
    intervalId = setInterval(() => {
      if (!isUpdating) {
        isUpdating = true
        updateStories()
        isUpdating = false
      }
    }, 60 * 1000)
  }, [])

  function onReloadButton() {
    clearInterval(intervalId)
    updateStories()
  }

  function updateStories() {
    setLoading(true)
    const fetchData = async (): Promise<IStory[]> => {
      const res = await getLastStories()
      console.log(res)
      return res
    }
    fetchData().then(res => {
      setLoading(false)
      return setNews(res);
    })
    .catch()
  }


  async function getLastStories() {
    const res = await fetchLastStories();
    res.sort( (a,b) => { return b.time - a.time } )
    return res
  }

  // styles

  const mainStyle = {
    padding: '1px',
  }

  const menuStyle = {
    backgroundColor: 'rgb(255, 102, 0)',
    padding: '20px',
    display: 'flex',
    justifyContent: 'center',
    color: 'white',
    fontSize: '7vh',
    marginTop: '10px',
    borderRadius: '20px'
  };

  const buttonStyle = {
    color: 'default',
    backgroundColor: '#1976d2',
    margin: '20px 0'
  }

  const menuItem = {
    padding: '25px',
    backgroundColor: '#E9DD9F',
    borderRadius: '20px',
    display: 'block',
    marginBottom: '10px',
    textDecoration: 'unset',
    color: 'black'
  }

  const itemTitle = {
    fontWeight: '600',
    fontSize: '25px'
  }

  const itemUndertitle = {
    display: 'flex',
    margin: '0 -5px'
  }

  const scoreStyle  = {
    display: 'flex',
    margin: '0 5px',
    alignItems: 'center'
  }

  const byStyle  = {
    display: 'flex',
    margin: '0 5px',
    alignItems: 'center'
  }

  const timeStyle  = {
    display: 'flex',
    margin: '0 5px',
    alignItems: 'center'
  }

  const commentsStyle  = {
    display: 'flex',
    margin: '0 5px',
    alignItems: 'center'
  }

  return (
    <div className="main" style={mainStyle}>
      <div className="menu" style={menuStyle}>
        News
      </div>
      
      <LoadingButton variant="contained" style={buttonStyle} loading={loading} loadingPosition="end" onClick={onReloadButton} endIcon={<Replay />}>Force reload</LoadingButton>
      <div className="menu-list">
        {news.map(item => (
          <Link to={"/news/" + item.id} key={item.id} className="menu-item" style={menuItem} state={item}>
            <div className="item-title" style={itemTitle}>{item.title}</div>
            <div className="item-undertitle" style={itemUndertitle}>
              <div className="score" style={scoreStyle}>
                <StarIcon sx={{ color: orange[500] }}/>
                {item.score}
              </div>
              <div className="by" style={byStyle}>
                By {item.by}
              </div>
              <div className="time" style={timeStyle}>
                {new Date(item.time * 1000).toLocaleString()}
              </div>
              <div className="comments" style={commentsStyle}>
                {item.descendants} Comments
              </div>
            </div>
          </Link>
        ))}
      </div>
      
    </div>
  );
}

export default Main;
