import { useEffect, useState } from "react";
import LoadingButton from '@mui/lab/LoadingButton';
import { fetchStory } from "../../services/storyService";
import { IStory } from "../../types/IStory";
import Replay from '@mui/icons-material/ReplayRounded'
import { useLocation } from "react-router-dom";
import { Link } from 'react-router-dom';
import { Button } from "@mui/material";
import Comment from "./Comment/Comment";

const News = function () {
  const location = useLocation();
  const item = location.state;
  console.log(item)

  const [story, setStory] = useState({} as IStory)
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    updateComments()
    // eslint-disable-next-line
  }, [])

  function onReloadButton() {
    updateComments()
  }

  function updateComments() {
    setLoading(true)
    const fetchData = async (): Promise<IStory> => {
      const res = await getStory()
      console.log(res)
      return res
    }
    fetchData().then(res => {
      setLoading(false)
      return setStory(res);
    })
    .catch()
  }

  async function getStory() {
    const res = await fetchStory(item.id);
    return res
  }

  // styles

  const mainStyle = {
    padding: '1px',
  }

  const menuStyle = {
    backgroundColor: 'rgb(255, 102, 0)',
    padding: '20px',
    display: 'flex',
    justifyContent: 'center',
    color: 'white',
    fontSize: '4vh',
    marginTop: '10px',
    borderRadius: '20px'
  };

  const buttonStyle = {
    color: 'default',
    backgroundColor: '#1976d2',
    margin: '20px 0'
  }

  const bodyStyle = {
    padding: '25px',
    backgroundColor: '#E9DD9F',
    borderRadius: '20px',
    marginBottom: '10px'
  }

  const itemUndertitle = {
    display: 'flex',
    margin: '0 -5px'
  }

  const byStyle  = {
    display: 'flex',
    margin: '0 5px',
    alignItems: 'center'
  }

  const timeStyle  = {
    display: 'flex',
    margin: '0 5px',
    alignItems: 'center'
  }

  const commentsStyle  = {
    display: 'flex',
    margin: '0 5px',
    alignItems: 'center'
  }

  const comments = {
    backgroundColor: 'white',
    padding: '20px',
    borderRadius: '15px',
  }

  return (
    <div className="main" style={mainStyle}>
      <div className="menu" style={menuStyle}>
        {item.title}
      </div>

      <Link to={"/"} ><Button variant="contained" style={buttonStyle}>Go back</Button></Link>

      <LoadingButton variant="contained" style={{...buttonStyle, marginLeft: '20px'}} loading={loading} loadingPosition="end" onClick={onReloadButton} endIcon={<Replay />}>Reload Comments</LoadingButton>

      <div className="body" style={bodyStyle}>
        <div className="item-undertitle" style={itemUndertitle}>
          <div className="by" style={byStyle}>
            By {item.by}
          </div>
          <div className="time" style={timeStyle}>
            {new Date(item.time * 1000).toLocaleString()}
          </div>
          <div className="comments" style={commentsStyle}>
            {story.descendants} Comments
          </div>
        </div>
        {item.url?
        (
          <div className="item-url" style={{display: 'flex'}}>
            <div className="text">Url</div>
            <a href={item.url} className="url" style={{marginLeft: '10px'}}>{item.url}</a>
          </div>
        )
        :
        (
          <div>
            without url
          </div>
        )
        }
        <br />
        <div>
          {item.text}
        </div>

        <h3>Comments</h3>
        <div className="comments" style={comments}>
          { loading?
            <div>Loading comments...</div>
            :
            <>{ story.comments?.length?
              (
                <div>
                  {story.comments.map(comment => <Comment key={comment.id} data={comment} />)}
                </div> 
              )
              :
              ( <div>No comments</div> )
            }</>
          }
          
        </div>
    </div>
  </div>
  );
}

export default News;
