import { useState } from "react";
import LoadingButton from '@mui/lab/LoadingButton';
import { fetchStory } from "../../../services/storyService";
import { IStory } from "../../../types/IStory";

const Comment = function (props: any) {
  const data = props.data
  console.log(data)

  const [childs, setChilds] = useState([] as IStory[])
  const [loading, setLoading] = useState(false);

  function openChilds() {
    setLoading(true)
    const fetchData = async (): Promise<IStory> => {
      const res = await getStory()
      return res
    }
    fetchData().then(res => {
      setLoading(false)
      return setChilds(res.comments);
    })
    .catch()
  }

  async function getStory() {
    const res = await fetchStory(data.id);
    return res
  }

  // styles

  const mainStyle = {
    padding: '1px',
    margin: '10px 0'
  }

  return (
    <div className="comment" style={mainStyle}>
      <div style={{display: 'flex', margin: '0 -5px', color: 'gray'}}>
        <div style={{margin: '0 5px'}}>
          {data.deleted?'Deleted user':data.by}
        </div>
        <div style={{margin: '0 5px'}}>
          {new Date(data.time * 1000).toLocaleString()}
        </div>
      </div>
      <div dangerouslySetInnerHTML={{__html: data.deleted?'<Deleted comment>':data.text}}>
      </div>
      {childs.length?
        <div style={{marginLeft: '20px'}}>{childs.map(child => <Comment key={child.id}  data={child} />)}</div>
        :
        <>{data.kids?
          <LoadingButton size="small" onClick={openChilds} loading={loading}>open answers...</LoadingButton>
          :
          <></>
        }</>
      }
    </div>
  );
}

export default Comment;
